.header {
  padding-top: 0.3rem;
  background-color: rgba(24, 24, 24, 0.8);
}

.hr_header {
  background-color: var(--color-green);
  border: none;
  height: 1px;
}
.nav_header {
  width: 100%;
  background-color: rgba(24, 24, 24, 0.8);
}
.nav_header ul {
  display: flex;
  justify-content: space-around;
  align-items: center;

  list-style: none;
}
.ul_projetos li {
  margin: auto;
  width: 100%;
  height: 100%;
}

.ul_projetos li a {
  text-align: center;
  margin: auto;
  display: block;
  width: 100%;
  height: 100%;
  padding: 2rem;
  text-decoration: none;
}
.ul_projetos li a:hover {
  background-color: rgba(27, 27, 27, 0.8);
  text-decoration: underline;
  filter: brightness(120%);
  cursor: pointer;
}

.ul_projetos li a:visited {
  color: var(--color-p);
}

.ul_projetos li a:link {
  color: var(--color-p);
}
.main_projetos {
  display: flex;
  flex-direction: column;
  gap: calc(85vh - 80vh);
  width: 100%;
  height: 85vh;
}
.section_projetos {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
  padding: 1rem;
}
